<template>
    <div class="pages_content">
        <van-nav-bar :fixed="true" class="common-nav-bar">
            <template #left>
                <img src="../../assets/img/user_icon.png" @click="$router.push({path: '/mine'})" style="height: 18px; width: 18px" alt="">
            </template>
            <template #title>
                {{$store.state.vuex.stationData.text || ''}}<span class="navBarxl" @click="$router.push('/stationListSelect')"></span>
            </template>
            <template #right>
<!--                <img src="../../assets/img/message.png" style="height: 17.5px; width: 19.5px" alt="">-->
            </template>
        </van-nav-bar>
        <div class="bg_fullscreen">
            <div class="cloud">
                <img src="../../assets/img/home/yun.png" class="yun" alt="">
                <img src="../../assets/img/home/yun1.png" class="yun1" alt="">
            </div>
            <p class="update">{{realTimeData.spt}}</p>
            <div class="menu_list">
                <ul>
                    <li @click="goto(1)"><img src="../../assets/img/home/wrrl.png" style="width: 43.2px; height: 43.8px" alt=""><br></li>
                    <li @click="goto(2)"><img src="../../assets/img/home/jrdb.png" style="width: 43.2px; height: 43.8px" alt=""><br></li>
                    <li @click="goto(3)"><img src="../../assets/img/home/jrfz.png" style="width: 43.2px; height: 43.8px" alt=""><br></li>
<!--                    <li @click="goto(4)"><img src="../../assets/img/home/jrbj.png" style="width: 43.2px; height: 43.8px" alt=""><br></li>-->
                </ul>
            </div>
            <div class="svgCanvas">
                <circleCom v-if="showYBP" :options="options" :value="realTimeData.percent" :pollutionLevel="realTimeData.airQuality"></circleCom>
                <div class="svgContent">
                    <span class="aqi">AQI</span>
                    <span class="aqiValue">{{realTimeData.aqi || '--'}}</span>
                    <span class="waste"><font>{{realTimeData.primaryPollution || '--'}}</font></span>
                </div>
                <div class="svgDesc">
                    <ul>
                        <li><span><img src="../../assets/img/tep.png" style="width: 12px; height: 16px; margin-right: 5px">{{realTimeData.atmp || '--'}}</span></li>
                        <li><span><img src="../../assets/img/icon/yd.png" style="width: 11.2px; height: 14.3px; margin-right: 5px" alt="">&nbsp;{{realTimeData.hum || '--'}}%</span></li>
                    </ul>
                </div>
            </div>
            <div class="actionCanvas">
                <div class="boat">
                    <img src="../../assets/img/home/lingling.png" style="width: 71.5px; height: 111px;margin-top: 145px; margin-left: 60px" alt="">
                </div>
                <div class="aqiContent">
<!--                    <p class="aqiTitle">AQI <font>56</font>,  <font>PM2.5</font></p>-->
                    <ul>
                        <li v-for="(item,index) in factorList" :key="index">{{realTimeData[item.cd] || '--'}} <span class="factor" :class="'level'+(((realTimeData[item.cd+'_level'] && realTimeData[item.cd+'_level']!=-1) || realTimeData[item.cd+'_level']==0) ? (realTimeData[item.cd+'_level'] + 1) : 7)">{{item.monitoring_factor_nm}}</span></li>
                    </ul>
                </div>
            </div>
        </div>
        <div class="chartsFormData">
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> {{recentText}}</div>
                    <div class="right"><span :class="selectActive[0][0]" @click="changeItem(0,0)">24</span> <span :class="selectActive[0][1]" @click="changeItem(0,1)">30</span></div>
                </div>
                <div class="content">
                    <div class="echarts" id="echarts">

                    </div>
                    <div class="e_select">
                        <ul>
                            <li :class="factorActive[0]" @click="changeFactor(0, 'aqi')">AQI</li>
                            <li :class="factorActive[1]" @click="changeFactor(1, 'pm25')">PM <sub>2.5</sub></li>
                            <li :class="factorActive[2]" @click="changeFactor(2, 'pm10')">PM <sub>10</sub></li>
                            <li :class="factorActive[3]" @click="changeFactor(3, 'so2')">SO <sub>2</sub></li>
                            <li :class="factorActive[4]" @click="changeFactor(4, 'o3')">O <sub>3</sub></li>
                            <li :class="factorActive[5]" @click="changeFactor(5, 'no2')">NO <sub>2</sub></li>
                            <li :class="factorActive[6]" @click="changeFactor(6, 'co')">CO</li>
                        </ul>
                    </div>
                </div>
            </div>
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> {{new Date().getFullYear()}}</div>
                    <div class="right"><span :class="selectActive[1][0]" @click="changeItem(1,0)"></span> <span :class="selectActive[1][1]" @click="changeItem(1,1)"></span></div>
                </div>
                <div class="content normal">
                    <div class="echarts" id="echarts2">

                    </div>
                    <table border="0" cellspacing="0" cellpadding="0">
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr v-for="(item,index) in goodfineData.data" :key="index">
                            <td><span :class="'level'+index"></span>{{item.name}}</td>
                            <td>{{item.percent}}</td>
                            <td>{{item.value}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> {{new Date().getFullYear()}}</div>
                    <div class="right"><span :class="selectActive[2][0]" @click="changeItem(2,0)"></span> <span :class="selectActive[2][1]" @click="changeItem(2,1)"></span></div>
                </div>
                <div class="content normal">
                    <div class="echarts" id="echarts3">

                    </div>
                    <table border="0" cellspacing="0" cellpadding="0">
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr v-for="(item,index) in comprehensiveData.data" :key="index">
                            <td><span :class="item.name"></span><font v-html="item.html"></font></td>
                            <td>{{item.percent}}</td>
                            <td>{{item.value}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> {{new Date().getFullYear()}}</div>
                    <div class="right"><span :class="selectActive[3][0]" @click="changeItem(3,0)"></span> <span :class="selectActive[3][1]" @click="changeItem(3,1)"></span></div>
                </div>
                <div class="content normal">
                    <div class="echarts" id="echarts4">

                    </div>
                    <table border="0" cellspacing="0" cellpadding="0">
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr v-for="(item,index) in firstPolluteData.data" :key="index">
                            <td><span :class="item.name"></span><font v-html="item.html"></font></td>
                            <td>{{item.percent}}</td>
                            <td>{{item.value}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> {{new Date().getFullYear()}}</div>
                    <div class="right"><span :class="selectActive[4][0]" @click="changeItem(4,0)"></span> <span :class="selectActive[4][1]" @click="changeItem(4,1)"></span></div>
                </div>
                <div class="content normal">
                    <div class="echarts" id="echarts5">

                    </div>
                    <table border="0" cellspacing="0" cellpadding="0">
                        <tr>
                            <td></td>
                            <td></td>
                            <td></td>
                        </tr>
                        <tr v-for="(item,index) in standardData.data" :key="index">
                            <td><span :class="item.name"></span><font v-html="item.html"></font></td>
                            <td>{{item.percent}}</td>
                            <td>{{item.value}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> </div>
                    <div class="right"><span :class="selectActive[5][0]" @click="changeItem(5,0)"></span> <span :class="selectActive[5][1]" @click="changeItem(5,1)"></span></div>
                </div>
                <div class="content">
                    <div class="e_select">
                        <p class="note">COmgg</p>
                        <table border="0" cellspacing="0" cellpadding="0">
                            <tr>
                                <td></td>
                                <td></td>
                                <td></td>
                            </tr>
                            <tr v-for="(item,index) in airData" :key="index">
                                <td>{{item.item}}</td>
                                <td>{{item.case}}</td>
                                <td>{{item.dq_tb != "--" ? Math.abs(item.dq_tb) : item.dq_tb}} <template v-if="item.dq_tb != '--' && Number(item.dq_tb) != 0"><font class="down" v-if="Number(item.dq_tb) < 0"></font><font class="up" v-else></font></template></td>
                            </tr>
                        </table>
                    </div>
                </div>
            </div>
            <div class="item">
                <div class="title">
                    <div class="left"><span></span> </div>
                    <div class="right"><span class="active selectStation" @click="selectPicker(2)">{{factorValue}} <span class="triangleDownStation"></span></span></div>
                </div>
                <div class="content factor">
                    <div class="echarts" id="echarts6">

                    </div>
                </div>
            </div>
        </div>
<!--        <van-popup v-model="stationPicker" position="bottom">-->
<!--            <van-picker-->
<!--                show-toolbar-->
<!--                :columns="stationColumns"-->
<!--                @confirm="onStationConfirm"-->
<!--                @cancel="stationPicker = false"-->
<!--            />-->
<!--        </van-popup>-->
        <van-popup v-model="factorPicker" position="bottom">
            <van-picker
                show-toolbar
                :columns="factorColumns"
                @confirm="onFactorConfirm"
                @cancel="factorPicker = false"
            />
        </van-popup>
    </div>
</template>

<script>
    import circleCom from "../../components/circle";
    export default {
        name: "home",
        components: {
            circleCom
        },
        data () {
          return {
            options: {
              srtokeWidth: 2.4  // svg
            },
            realTimeData: {}, // 
            recentText: "24",
            recentlyTime: {
              startTime: '',
              endTime: '',
              timeType: '',
              factor: 'aqi',
              mnType: "city"
            },
            goodAndFineTime: {
              startTime: '',
              endTime: '',
              mnType: 'city',
              timeType: '',
              mns: ''
            },
            comprehensiveTime: {
              startTime: '',
              endTime: '',
              mnType: 'city',
              timeType: '',
              mns: ''
            },
            firstPolluteTime: {
              startTime: '',
              endTime: '',
              mnType: 'city',
              timeType: '',
              mns: ''
            },
            standardTime: {
              startTime: '',
              endTime: '',
              mnType: 'city',
              timeType: '',
              mns: ''
            },
            airDataTime: {
              startTime: '',
              endTime: '',
              mnType: 'city',
              timeType: '',
              mns: ''
            },
            tbTime: {
              startTime: '',
              endTime: '',
              mnType: 'city',
              timeType: '',
              factor: "aqi",
              mns: ''
            },
            recentData: {},
            goodfineData: {
              data: []
            },
            comprehensiveData: {
              data: []
            },
            firstPolluteData: {
              data: []
            },
            standardData: {
              data: []
            },
            airData: [],
            tbData: [],
            showYBP: false,
            selectActive: [["active"],["active"],["active"],["active"],["active"],["active"]],
            factorActive: ["active"],
            lineHoursEcharts: {}, // 24
            countDayEcharts: {}, // 2020
            pieContribution: {}, // 2020
            firstWaste: {}, // 2020
            exceedDay: {}, // 2020
            monthStatic: {}, // 
            factorList: [], // 
            factorValue: "AQI", // 
            factorPicker: false, // 
            factorColumns: ["AQI", 'PM2.5', 'PM10', 'SO2', 'O3', 'NO2', 'CO'], // 
            colorList: ["#FFCF3F","#5CDFD5","#D2F13C","#55DE38","#FF7840","#3E9AF7"],
            htmls: ["PM <sub>2.5</sub>", "PM <sub>10</sub>", "SO <sub>2</sub>", "O <sub>3</sub>", "NO <sub>2</sub>", "CO"],
            nameList: ["pm25", "pm10", "so2", "o3", "no2", "co"]
          }
        },
      activated() {
        // 
        this.getMonitorFactor();
        // 
        this.getRealTimeAirQuality();
        // 24
        this.getRecentlyRealData();
        // 
        this.getGoodAndFineDays();
        // 
        this.getComprehensive();
        // 
        this.getFirstPollute();
        // 
        this.getStandardDays();
        // 
        this.getAirDataStatus();
        // 
        this.getTbStatisticData();
      },
      mounted() {
        let date = new Date()
        this.recentlyTime.endTime      = date.format("yyyyMMddhh")
        this.goodAndFineTime.endTime   = date.format("yyyyMMddhh")
        this.goodAndFineTime.startTime = date.format("yyyy010100")
        this.comprehensiveTime.startTime = date.format("yyyy010100")
        this.comprehensiveTime.endTime   = date.format("yyyyMMddhh")
        this.firstPolluteTime.startTime  = date.format("yyyy010100")
        this.firstPolluteTime.endTime    = date.format("yyyyMMddhh")
        this.standardTime.startTime  = date.format("yyyy010100")
        this.standardTime.endTime    = date.format("yyyyMMddhh")
        this.airDataTime.startTime  = date.format("yyyy010100")
        this.airDataTime.endTime    = date.format("yyyyMMddhh")
        this.tbTime.endTime      = date.format("yyyyMMddhh")
        this.tbTime.startTime    = date.format("yyyy010100")
        date.setTime(date.getTime()-24*60*60*1000) // 24
        this.recentlyTime.startTime = date.format("yyyyMMddhh")
        },
        methods: {
          // 
          getMonitorFactor(){
            let params = {
              mns: ""
            }
            if( this.$store.state.vuex.stationData.id ) {
              params.mns = this.$store.state.vuex.stationData.id
            }
            this.$http.get("/AirAppXY-Service/map/queryStationMontFactors", {params:params}).then(res=>{
              if( res.data.code == 200 ) {
                this.factorList = res.data.content.info
              }
            })
          },
          // 
          goto(number){
            if( number == 1 ) {
                this.$router.push('/calendar')
            } else if( number == 2 ) {
                this.$router.push('/standard')
            } else if( number == 3 ) {
                this.$router.push('/peakvalue')
            } else if( number == 4 ) {
                this.$router.push('/report')
            }
          },
          // 24
          drawLineHoursData(){
            let that = this
            this.lineHoursEcharts = this.$echarts.init(document.getElementById("echarts"))
            let option  = {
              color: "#E5CE10",
              xAxis: {
                type: 'category',
                axisTick: {
                  alignWithLabel: true,
                  show: false,
                },
                axisLine: {
                  lineStyle: {
                    color: '#ddd', // 
                    width: 1 // 
                  },
                },
                axisLabel: {
                  rotate: 40,
                  color: "#666"
                },
                data: this.recentData.time
              },
              tooltip: {
                trigger: 'axis'
              },
              grid: {
                left: '3%',
                right: '8%',
                bottom: '3%',
                top: '10%',
                containLabel: true
              },
              yAxis: {
                type: 'value',
                axisLine: {
                  show: false
                },
                axisTick: {
                  show: false
                },
                splitLine:{
                  show: true,
                  lineStyle: {
                    color: "#ddd"
                  }
                }
              },
              series: [{
                name: this.recentlyTime.factor,
                data: this.recentData.data,
                type: 'bar',
                barWidth: 7,
                markLine: {
                  symbol: ['none', 'none'],//
                  itemStyle: {
                      normal: {
                          lineStyle: {
                              width: 2,
                              type: 'solid',
                              color:'#E6A23C'
                          },
                          label: {
                              color:'#F56C6C',
                              show: true,
                              position:'end',
                              marginRight: '20',
                          }
                      }
                  },
                  data: [{
                      yAxis: that.recentData.standardLimit
                  }]
                },
                itemStyle: {
                  normal: {
                    //
                    color: function(params) {
                      switch (that.recentData.level[params.dataIndex]) {
                        case 0:
                          return "#24C768"
                          break;
                        case 1:
                          return "#E5CE10"
                          break;
                        case 2:
                          return "#FF7E00"
                          break;
                        case 3:
                          return "#FF0000"
                          break;
                        case 4:
                          return "#990000"
                          break;
                        case 5:
                          return "#7E0000"
                          break;
                      }
                    }
                  }
                },
                backgroundStyle: {
                  color: 'rgba(220, 220, 220, 0.8)'
                }
              }]
            }
            this.lineHoursEcharts.setOption(option)
          },
          // 2020
          drawPieCountDay(){
            let that = this
            this.countDayEcharts = this.$echarts.init(document.getElementById("echarts2"))
            let colorList= ["#24C768","#E5CE10","#FF7E00","#FF0000","#990000","#7E0000"]
            let levelList= ["","","","","",""]
            this.goodfineData.data.map((item,index)=>{
              let obj       = {}
              obj.value     = item
              obj.name      = levelList[index]
              obj.itemStyle = {
                normal: {color: colorList[index]}
              }
              this.goodfineData.data[index] = obj
            })
            let option2 = {
              series: [
                {
                  name: '',
                  type: 'pie',
                  radius: ['45%', '65%'],
                  avoidLabelOverlap: false,
                  selectedMode: false,
                  label: {
                    normal: {
                      formatter: '\n{a|'+(this.goodfineData.data[0].value + this.goodfineData.data[1].value)+'}\n'+this.goodfineData.tb+'',
                      rich: {
                        a: {
                          color: '#1A1A1A',
                          lineHeight: 22,
                          fontFamily: "PingFang SC",
                          align: 'center',
                          fontSize: 12
                        }
                      },
                      textStyle: {
                        color: "#666666",
                        fontWeight: "normal",
                        fontSize: 9
                      },
                      position: "center",
                      show: true
                    }
                  },
                  emphasis: {
                    show: false
                  },
                  labelLine: {
                    show: false
                  },
                  data: this.goodfineData.data
                },
                {
                  type: 'pie',
                  radius: ['45%', '65%'],
                  label: {
                    position: 'outside',
                    normal: {
                      formatter: function(data){
                        let percent = data.percent.toFixed(1)+"%";
                        that.goodfineData.data[data.dataIndex].percent = percent
                        if( data.percent > 0 ) {
                          return percent;
                        } else {
                          return ""
                        }
                      } ,
                      textStyle: {
                        fontWeight: 'normal',
                        fontSize: 10,
                        color: '#666'
                      }
                    }
                  },
                  labelLine: {
                    show: false,
                    length: 0,
                    length2: 0,
                  },
                  data: this.goodfineData.data
                }
              ]
            }
            this.countDayEcharts.setOption(option2)
          },
          // 2020
          drawPieContribution(){
            let that = this
            this.pieContribution = this.$echarts.init(document.getElementById("echarts3"))
            let value    = 0
            this.comprehensiveData.data.map((item,index)=>{
              let obj       = {}
              obj.value     = item
              value        += item
              obj.name      = this.nameList[index]
              obj.html      = this.htmls[index]
              obj.itemStyle = {
                normal: {color: this.colorList[index]}
              }
              this.comprehensiveData.data[index] = obj
            })
            let option2 = {
              series: [
                {
                  name: '',
                  type: 'pie',
                  radius: ['45%', '65%'],
                  avoidLabelOverlap: false,
                  selectedMode: false,
                  label: {
                    normal: {
                      formatter: '\n{a|'+value+'}\n'+this.comprehensiveData.tb,
                      rich: {
                        a: {
                          color: '#1A1A1A',
                          lineHeight: 22,
                          fontFamily: "PingFang SC",
                          align: 'center',
                          fontSize: 12
                        }
                      },
                      textStyle: {
                        color: "#666666",
                        fontWeight: "normal",
                        fontSize: 9
                      },
                      position: "center",
                      show: true
                    }
                  },
                  emphasis: {
                    show: false
                  },
                  labelLine: {
                    show: false
                  },
                  data: this.comprehensiveData.data
                },
                {
                  type: 'pie',
                  radius: ['45%', '65%'],
                  label: {
                    position: 'outside',
                    normal: {
                      formatter: function(data){
                        let percent = data.percent.toFixed(1)+"%";
                        that.comprehensiveData.data[data.dataIndex].percent = percent
                        if( data.percent > 0 ) {
                          return percent;
                        } else {
                          return ""
                        }
                      } ,
                      textStyle: {
                        fontWeight: 'normal',
                        fontSize: 10,
                        color: '#666'
                      }
                    }
                  },
                  labelLine: {
                    show: false,
                    length: 1,
                    length2: 1,
                  },
                  data: this.comprehensiveData.data
                }
              ]
            }
            this.pieContribution.setOption(option2)
          },
          // 2020
          drawPieFirstWaste(){
            let that = this
            this.firstWaste = this.$echarts.init(document.getElementById("echarts4"))
            this.firstPolluteData.data.map((item,index)=>{
              let obj       = {}
              obj.value     = item
              obj.name      = this.nameList[index]
              obj.html      = this.htmls[index]
              obj.itemStyle = {
                normal: {color: this.colorList[index]}
              }
              this.firstPolluteData.data[index] = obj
            })
            let option2 = {
              series: [
                {
                  name: '',
                  type: 'pie',
                  radius: ['45%', '65%'],
                  avoidLabelOverlap: false,
                  selectedMode: false,
                  label: {
                    normal: {
                      position: "center",
                      show: false
                    }
                  },
                  emphasis: {
                    show: false
                  },
                  labelLine: {
                    show: false
                  },
                  data: this.firstPolluteData.data
                },
                {
                  type: 'pie',
                  radius: ['45%', '65%'],
                  label: {
                    position: 'outside',
                    normal: {
                      formatter: function(data){
                        let percent = data.percent.toFixed(1)+"%";
                        that.firstPolluteData.data[data.dataIndex].percent = percent
                        if( data.percent > 0 ) {
                          return percent;
                        } else {
                          return ""
                        }
                      } ,
                      textStyle: {
                        fontWeight: 'normal',
                        fontSize: 10,
                        color: '#666'
                      }
                    }
                  },
                  labelLine: {
                    show: false,
                    length: 0,
                    length2: 0,
                  },
                  data: this.firstPolluteData.data
                }
              ]
            }
            this.firstWaste.setOption(option2)
          },
          // 2020
          drawPieExceedDay(){
            let that = this
            this.exceedDay = this.$echarts.init(document.getElementById("echarts5"))
            // let colorList= ["#24C768","#E5CE10","#FF7E00","#FF0000","#990000","#7E0000"]
            this.standardData.data.map((item,index)=>{
              let obj       = {}
              obj.value     = item
              obj.name      = this.nameList[index]
              obj.html      = this.htmls[index]
              obj.itemStyle = {
                normal: {color: this.colorList[index]}
              }
              this.standardData.data[index] = obj
            })
            let option2 = {
              series: [
                {
                  name: '',
                  type: 'pie',
                  radius: ['45%', '65%'],
                  avoidLabelOverlap: false,
                  selectedMode: false,
                  label: {
                    normal: {
                    //   formatter: '\n{a|6.133}\n1.009',
                    //   rich: {
                    //     a: {
                    //       color: '#1A1A1A',
                    //       lineHeight: 22,
                    //       fontFamily: "PingFang SC",
                    //       align: 'center',
                    //       fontSize: 12
                    //     }
                    //   },
                    //   textStyle: {
                    //     color: "#666666",
                    //     fontWeight: "normal",
                    //     fontSize: 9
                    //   },
                    //   position: "center",
                      show: false
                    }
                  },
                  emphasis: {
                    show: false
                  },
                  labelLine: {
                    show: false
                  },
                  data: this.standardData.data
                },
                {
                  type: 'pie',
                  radius: ['45%', '65%'],
                  label: {
                    position: 'outside',
                    normal: {
                      formatter: function(data){
                        let percent = data.percent.toFixed(1)+"%";
                        that.standardData.data[data.dataIndex].percent = percent
                        if( data.percent > 0 ) {
                          return percent;
                        } else {
                          return ""
                        }
                      },
                      textStyle: {
                        fontWeight: 'normal',
                        fontSize: 10,
                        color: '#666'
                      }
                    }
                  },
                  labelLine: {
                    show: false,
                    length: 1,
                    length2: 1,
                  },
                  data: this.standardData.data
                }
              ]
            }
            this.exceedDay.setOption(option2)
          },
          // 
          drawLineMonthStatic(){
            let year = new Date().getFullYear()
            this.monthStatic = this.$echarts.init(document.getElementById('echarts6'))
            let option = {
              // title: {
              //   text: '(g/m3)',
              //   textStyle: {
              //     color: '#666666',        // 
              //     fontStyle: 'normal',     // 
              //     fontWeight: 'normal',    // 
              //     fontFamily: 'Microsoft yahei',   // 
              //     fontSize: 14,     // 
              //     align: 'center'   // 
              //   },
              // },
              tooltip: {
                show: true
              },
              legend: [
                {
                  x: "left",
                  y: "top",
                  data: [(year-2)+''],
                },
                {
                  x: "25%",
                  y: "top",
                  data: [(year-1)+''],
                },
                {
                  x: "50%",
                  y: "top",
                  data: [year+''],
                }
              ],
              xAxis: {
                type: 'category',
                data: this.tbData.time,
                axisLabel: {
                  show: true,
                  type: 'category',
                  boundaryGap: false,
                  textStyle: {
                    color: "#666666",
                    fontSize: 12
                  },
                  lineStyle: {
                    color: '#ddd',
                  }
                },
                axisLine: {
                  lineStyle: {
                    color: '#ddd', // 
                    width: 1 // 
                  }
                },
                axisTick: {
                  show: false
                },
                splitLine: {
                  show: false,
                },
              },
              yAxis: {
                interval: 50,
                min: 0,
                max: 150,
                type: 'value',
                axisTick: {
                  show: false
                },
                axisLabel: {
                  color: '#666666'
                },
                axisLine: {
                  show: false
                },
              },
              grid: {
                top: "16%",
                left: "8%",
                bottom: "15%",
                right: "4%"
              },
              series: [{
                name: (year-2)+'',
                data: this.tbData.tbDatas,
                type: 'line',
                smooth: true,
                symbolSize: 8,   //
                itemStyle: {
                  normal: {
                    color: '#1EBCBC', //
                    lineStyle: {
                      color: '#1EBCBC' //
                    }
                  }
                }
              },{
                name: (year-1)+'',
                data: this.tbData.tbData,
                type: 'line',
                smooth: true,
                symbolSize: 8,   //
                itemStyle: {
                  normal: {
                    color: '#FF7017', //
                    lineStyle: {
                      color: '#FF7017' //
                    }
                  }
                }
              },{
                name: year+'',
                data: this.tbData.dqData,
                type: 'line',
                smooth: true,
                symbolSize: 8,   //
                itemStyle: {
                  normal: {
                    color: '#1EBC52', //
                    lineStyle: {
                      color: '#1EBC52' //
                    }
                  }
                }
              }]
            };
            this.monthStatic.setOption(option);
          },
          // 
          selectPicker(number){
            if( number == 1 ) {
              this.stationPicker = true
            } else if( number == 2 ) {
              this.factorPicker = true
            }
          },
          // 
          onStationConfirm(value, index) {
            console.log(index);
            this.stationValue = value;
            this.stationPicker = false;
          },
          // 
          onFactorConfirm(value, index){
            console.log(index);
            if( index == 0 ) {
              this.tbTime.factor = "aqi"
            } else {
              this.tbTime.factor = this.nameList[index-1]
            }
            this.factorValue = value;
            this.factorPicker = false;
            this.getTbStatisticData()
          },
          // 
          changeItem(key, index){
            this.$set(this.selectActive, key, [])
            this.$set(this.selectActive[key], index, "active")
            let date = new Date()
            if( key == 0 ) {
              if( index == 0 ) {
                this.recentText = "24"
                date.setTime(date.getTime()-24*60*60*1000) // 24
                this.recentlyTime.startTime = date.format("yyyyMMddhh")
                this.recentlyTime.timeType  = ""
              } else {
                this.recentText = "30"
                date.setTime(date.getTime()-30*24*60*60*1000) // 24
                this.recentlyTime.startTime = date.format("yyyyMMddhh")
                this.recentlyTime.timeType  = ""
              }
              this.getRecentlyRealData()
            } else if( key == 1 ) {
              if( index == 0 ) {
                this.goodAndFineTime.startTime = date.format("yyyy010100")
              } else {
                this.goodAndFineTime.startTime = date.format("yyyyMM0100")
              }
              this.getGoodAndFineDays()
            } else if( key == 2 ) {
              if( index == 0 ) {
                this.comprehensiveTime.startTime = date.format("yyyy010100")
                this.comprehensiveTime.timeType  = ""
              } else {
                this.comprehensiveTime.startTime = date.format("yyyyMM0100")
                this.comprehensiveTime.timeType  = ""
              }
              this.getComprehensive()
            } else if( key == 3 ) {
              if( index == 0 ) {
                this.firstPolluteTime.startTime = date.format("yyyy010100")
              } else {
                this.firstPolluteTime.startTime = date.format("yyyyMM0100")
              }
              this.getFirstPollute()
            } else if( key == 4 ) {
              if( index == 0 ) {
                this.standardTime.startTime = date.format("yyyy010100")
              } else {
                this.standardTime.startTime = date.format("yyyyMM0100")
              }
              this.getStandardDays()
            } else if( key == 5 ) {
              if( index == 0 ) {
                this.airDataTime.startTime = date.format("yyyy010100")
                this.airDataTime.timeType  = ""
              } else {
                this.airDataTime.startTime = date.format("yyyyMM0100")
                this.airDataTime.timeType  = ""
              }
              this.getAirDataStatus()
            }
          },
          // 
          changeFactor(key, name) {
            this.factorActive        = []
            this.factorActive[key]   = "active"
            this.recentlyTime.factor = name
            this.getRecentlyRealData()
          },
          // 
          getRealTimeAirQuality(){
            let params = {}
            if( this.$store.state.vuex.stationData.id ) {
              params.mns = this.$store.state.vuex.stationData.id
            } else {
              params.mns = ""
            }
            this.showYBP = false
            this.$http.get("/AirAppXY-Service/air/queryRealXingYiAQI", {params: params}).then(res=>{
              if( res.data.code == 200 ) {
                this.realTimeData = res.data.content.info.hasOwnProperty('spt') ? res.data.content.info : res.data.content.info[0]
                if( this.realTimeData.aqi ) {
                  let percent = Number(( this.realTimeData.aqi / 300 ).toFixed(1))
                  this.$set(this.realTimeData, "percent", percent)
                }
                this.showYBP = true
              }
            })
          },
          // 24
          getRecentlyRealData(){
            this.$http.get("/AirAppXY-Service/air/airLineData", {params:this.recentlyTime}).then(res=>{
              if( res.data.code == 200 ) {
                this.recentData = res.data.content.info
                // 24
                this.drawLineHoursData();
              }
            })
          },
          // 
          getGoodAndFineDays(){
            this.$http.get("/AirAppXY-Service/air/getAirLevelData", {params: this.goodAndFineTime}).then(res=>{
              if( res.data.code == 200 ) {
                let obj = res.data.content.info
                this.goodfineData.data =  [obj[0], obj[1], obj[2], obj[3], obj[4], obj[5]]
                this.goodfineData.tb   = obj['01TB']
                // 
                this.drawPieCountDay();
              }
            })
          },
          // 
          getComprehensive(){
            this.$http.get("/AirAppXY-Service/air/getAirCompositeIndex", {params: this.comprehensiveTime}).then(res=>{
              if( res.data.code == 200 ) {

                let obj = res.data.content.info
                this.comprehensiveData.data =  [obj.pm25, obj.pm10, obj.so2, obj.o3, obj.no2, obj.co]
                this.comprehensiveData.tb   =  obj['IndexDq_Tb']
                // 
                this.drawPieContribution();
              }
            })
          },
          // 
          getFirstPollute(){
            this.$http.get("/AirAppXY-Service/air/getAirPollutionData", {params: this.firstPolluteTime}).then(res=>{
              if( res.data.code == 200 ) {
                let obj = res.data.content.info
                this.firstPolluteData.data =  [obj.pm25, obj.pm10, obj.so2, obj.o3, obj.no2, obj.co]
                // 
                this.drawPieFirstWaste();
              }
            })
          },
          // 
          getStandardDays(){
            this.$http.get("/AirAppXY-Service/air/getAirStandardData", {params: this.standardTime}).then(res=>{
              if( res.data.code == 200 ) {
                let obj = res.data.content.info
                this.standardData.data =  [obj.pm25, obj.pm10, obj.so2, obj.o3, obj.no2, obj.co]
                // 2020
                this.drawPieExceedDay();
              }
            })
          },
          // 
          getAirDataStatus() {
            this.$http.get("/AirAppXY-Service/air/getAirQualityData",{params: this.airDataTime}).then(res=>{
                if(res.data.code == 200) {
                  this.airData = res.data.content.info
                }
            })
          },
          // 
          getTbStatisticData(){
            this.$http.get("/AirAppXY-Service/air/getAirTBAnalyze",{params:this.tbTime}).then(res=>{
              if( res.data.code == 200 ) {
                this.tbData = res.data.content.info
                // 
                this.drawLineMonthStatic();
              }
            })
          }
        },
        beforeRouteEnter(to,from,next){
          next(vm=>{
            if( vm.$store.state.vuex.stationData.id ) {
              vm.recentlyTime.mns       = vm.$store.state.vuex.stationData.id
              vm.recentlyTime.mnType    = "station"
              vm.goodAndFineTime.mns    = vm.$store.state.vuex.stationData.id
              vm.goodAndFineTime.mnType = "station"
              vm.comprehensiveTime.mns       = vm.$store.state.vuex.stationData.id
              vm.comprehensiveTime.mnType    = "station"
              vm.firstPolluteTime.mns       = vm.$store.state.vuex.stationData.id
              vm.firstPolluteTime.mnType    = "station"
              vm.standardTime.mns       = vm.$store.state.vuex.stationData.id
              vm.standardTime.mnType    = "station"
              vm.airDataTime.mns       = vm.$store.state.vuex.stationData.id
              vm.airDataTime.mnType    = "station"
              vm.tbTime.mns       = vm.$store.state.vuex.stationData.id
              vm.tbTime.mnType    = "station"
            } else {
              vm.recentlyTime.mns       = ""
              vm.recentlyTime.mnType    = "city"
              vm.goodAndFineTime.mns    = ""
              vm.goodAndFineTime.mnType = "city"
              vm.comprehensiveTime.mns       = ""
              vm.comprehensiveTime.mnType    = "city"
              vm.firstPolluteTime.mns       = ""
              vm.firstPolluteTime.mnType    = "city"
              vm.standardTime.mns       = ""
              vm.standardTime.mnType    = "city"
              vm.airDataTime.mns       = ""
              vm.airDataTime.mnType    = "city"
              vm.tbTime.mns       = ""
              vm.tbTime.mnType    = "city"
            }
          })
        }
    }
</script>